import React, { Component } from "react";
import ProgressBar from "./ProgressBar";

class Languages extends Component {
  state = {
    sig: [
      { id: 1, value: "Arcgis", xp: 5.5 }, //1an=0,5 après la barre 1an
      { id: 2, value: "QGIS", xp: 4.3 }, //1an=2,8 avant la barre 1an
      { id: 3, value: "Erdas", xp: 4 },
      { id: 4, value: "Map info", xp: 2.8 },
    ],
    languages: [
      { id: 1, value: "Javascript", xp: 1.8 },
      { id: 2, value: "Css", xp: 2 },
      { id: 3, value: "Php", xp: 0.7 },
      { id: 4, value: "Python", xp: 0.4 },
    ],
    frameworks: [
      { id: 1, value: "React", xp: 1.4 },
      { id: 2, value: "Bootstrap", xp: 2 },
      { id: 3, value: "Sass", xp: 1.5 },
      { id: 4, value: "Openlayers", xp: 1.5 },
    ],
  };

  render() {
    let { sig, languages, frameworks } = this.state;

    return (
      <div className="languagesFrameworks">
        <ProgressBar
          languages={sig}
          className="languagesDisplay"
          title="Logiciels SIG"
        />
        <ProgressBar
          languages={languages}
          className="languagesDisplay"
          title="Languages"
        />
        <ProgressBar
          languages={frameworks}
          title="framework & bibliothèques"
          className="frameworksDisplay"
        />
      </div>
    );
  }
}

export default Languages;
