import React from "react";
import Navigation from "../components/Navigation";

const contact = () => {
  return (
    <div className="contact">
      <Navigation />
      <div className="contactContent">
        <div className="header"></div>
        <div className="contactBox">
          <h1>Contactez-moi</h1>
          <ul>
            <li>
              <i className="fas fa-map-marker-alt">
                <span>Bamako</span>
              </i>
            </li>
            <li>
              <i className="fas fa-mobile-alt"></i>
              <copyToClipboard text="0694148949">
                <span
                  className="clickInput"
                  onClick={() => {
                    alert("Téléphone copié !");
                  }}
                >
                  0694148949
                </span>
              </copyToClipboard>
            </li>
            <li>
              <i className="fas fa-envelope"></i>
              <copyToClipboard text="bagayokoibrahim@gmail.com">
                <span
                  className="clickInput"
                  onClick={() => {
                    alert("Email copié !");
                  }}
                >
                  bagayokoibrahim@gmail.com
                </span>
              </copyToClipboard>
            </li>
          </ul>
        </div>
        <div className="socialNetwork">
          <ul>
            <a
              href=" https://www.linkedin.com/in/ibrahim-bagayoko-30b1099b/"
              target="_blamk"
              rel="noopener noreferrer"
            >
              <h4>LinkedIn</h4>
              <i className="fab fa-linkedin"></i>
            </a>
            <a
              href=" https://github.com/"
              target="_blamk"
              rel="noopener noreferrer"
            >
              <h4>Github</h4>
              <i className="fab fa-github"></i>
            </a>
            <a
              href=" https://twitter.com/"
              target="_blamk"
              rel="noopener noreferrer"
            >
              <h4>Twitter</h4>
              <i className="fab fa-twitter"></i>
            </a>
            <a
              href="https://codepen.io/bagaib/pen/yLaxeEz?editors=1111"
              target="_blamk"
              rel="noopener noreferrer"
            >
              <h4>Codepen</h4>
              <i className="fab fa-codepen"></i>
            </a>
          </ul>
        </div>
      </div>
    </div>
  );
};

export default contact;
