import React from "react";

const Hobbies = () => {
  return (
    <div className="hobbies">
      <h3>Interrêts</h3>
      <ul>
        <li className="hobby">
          <i className="fas fa-running"></i>
          <span>Travaux champêtre</span>
        </li>
        <li className="hobby">
          <i className="fas fa-hiking"></i>
          <span>Course à pied</span>
        </li>
      </ul>
    </div>
  );
};

export default Hobbies;
